import evcTabbar from '@/components/evcTabbar.vue'
export default {
	components: {

		evcTabbar
	},
	data() {
		return {
			fontColor2: '#0099FF',
			walletImgSelect: '../../../static/images/evctabbar/walletselect.png',
			money: '',
			coinList: [],
			balanceList: []
		};
	},

	onLoad(options) {
		console.log(uni.getStorageSync('token'))
		//币种余额
		uni.request({
			url: this.baseUrl + "/total-balance",
			header: {
				Authorization: uni.getStorageSync('token')
			},
			success: (res) => {
				console.log(res)
				if (this.$base1._indexOf(res.data.status)) {
					this.$base1._isLogin()
				} else if (res.data.status == 1) {
					this.money = res.data.data
				} else {
					uni.showToast({
						title: res.data.message,
						icon: 'none'
					})
				}

			}
		})
		//我的资产列表
		uni.request({
			url: this.baseUrl + "/coin-list",
			header: {
				Authorization: uni.getStorageSync('token')
			},
			success: (res) => {
				console.log(res)
				if (res.data.status == 1) {
					this.coinList = res.data.data
				} else {
					uni.showToast({
						title: res.data.message,
						icon: 'none'
					})
				}

			}
		})
		//获取币种余额
		uni.request({
			url: this.baseUrl + "/coin-balance",
			header: {
				Authorization: uni.getStorageSync('token')
			},
			success: (res) => {
				console.log(res)
				if (res.data.status == 1) {
					this.balanceList = res.data.data
					this.set_balance();
				} else {
					uni.showToast({
						title: res.data.message,
						icon: 'none'
					})
				}

			}
		})
	},
	methods: {
		set_balance() {
			var self = this;
			for (var i = 0; i < self.coinList.length; i++) {
				for (var j = 0; j < self.balanceList.length; j++) {
					if (self.coinList[i].Id == self.balanceList[j].CoinId) {
						self.coinList[i].Money = self.balanceList[j].Money;
						self.coinList[i].Forzen = self.balanceList[j].Forzen;
						self.coinList[i].Price = self.balanceList[j].Money * self.coinList[i].Price;
					}
				};
			};
			self.coinList = JSON.parse(JSON.stringify(self.coinList))

		},
		jumpToRecord() {
			uni.navigateTo({
				url: "./charging-record"
			})
		},
		jumpToTransferNum() {
			uni.navigateTo({
				url: "./transfer-num?money=" + this.money
			})
		},
		jumToQrcode() {
			uni.navigateTo({
				url: "./receivables-qrcode"
			})
		},
		jumpToMyBill() {
			uni.navigateTo({
				url: "../personal/my-bill"
			})
		},
		jumpTocurrencyDetail(index) {
			uni.navigateTo({
				url: "./currency-detail?coinId=" + this.coinList[index].Id + "&money=" + this.coinList[index].Money + "&forzen=" +
					this.coinList[index].Forzen + "&price=" + this.coinList[index].Price + "&logo=" + this.coinList[index].Logo
			})

		}

	}
}
